import React, { FC } from 'react';
import { Link } from 'gatsby';
import styled from 'styled-components';
import { TPost } from '../types/data';

type Props = {
  className?: string;
  post: TPost;
};

const Component: FC<Props> = ({ className, post }) => {
  const { title, updatedAt, image } = post.node;
  const { description } = post.node.description;
  const pageLink = `/post/${post.node.slug}`;
  return (
    <div className={className}>
      <Link to={pageLink} className="post-link-anchor">
        <div className="post-link">
          <div>
            <img src={image.file.url} className="post-link-image" alt="post-cover" />
          </div>
          <div className="post-link-text">
            <h2>{title}</h2>
            <p className="post-link-body">{description}</p>
            <p className="post-link-date">{updatedAt}</p>
          </div>
        </div>
      </Link>
    </div>
  );
};

const PostLink = styled(Component)`
  & .post-link {
    display: grid;
    margin: 50px 0;
  }

  & .post-link-body {
    display: -webkit-box;
    overflow: hidden;
    color: #434343;
    text-overflow: ellipsis;
    overflow-wrap: normal;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  & .post-link-text {
    padding: 1rem;
  }

  & .post-link-date {
    color: #595959;
  }

  & .post-link-image {
    width: 100%;
    height: 100%;
    max-height: 200px;
    object-fit: cover;
  }

  & .post-link-anchor {
    color: inherit;
    text-decoration: none;
  }

  @media screen and (max-width: 575px) {
    & .post-link {
      grid-template-columns: 1fr;
      border-bottom-right-radius: 10px;
      border-bottom-left-radius: 10px;
      box-shadow: rgba(0, 0, 0, 0.2) 0 20px 40px;
    }

    & .post-link-image {
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }
  }

  @media screen and (min-width: 576px) {
    & .post-link {
      grid-template-columns: 280px 1fr;
      grid-column-gap: 24px;
    }

    & .post-link-image {
      border-radius: 10px;
      box-shadow: 0 30px 60px -10px rgba(0, 0, 0, 0.3), 0 18px 36px -18px rgba(0, 0, 0, 0.33);
    }
  }
`;

export default PostLink;
